import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Select, TreeSelect, Input, message, Modal } from 'antd';
import { getAction } from '@/axios';

const Option = Select.Option;
const TreeNode = TreeSelect.TreeNode;

class CreateModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            name:"",
            type:"1",
            folderId:"",
            treeData:[]
        }
    }
    componentDidMount(){
        this.getTreeData();
    }
    //获取树的结构
    getTreeData = () => {
        getAction("/ybt/wf/process/queryFolderTree").then(res => {
            if(res.success){
                this.setState({
                    treeData:res.obj || []
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //渲染树
    renderTreeNodes = data => data.map(item => {
        if (item.children) {
        return (
            <TreeNode title={item.title} key={item.id+"key"} value={item.id}>
                {this.renderTreeNodes(item.children)}
            </TreeNode>
        );
        }
        return <TreeNode key={item.id+"key"} title={item.title} value={item.id}/>;
    });
    _changeName = (e) => {
        this.setState({
            name:e.target.value
        })
    }
    _changeFolder = (val) => {
        this.setState({
            folderId:val
        })
    }
    _buildFlowOk = () => {
        if (!this.state.name) {
            message.warn('请输入新建流程名称！！！')
            return  false
        } else if (!this.state.folderId) {
            message.warn('请选择所属文件夹！！！')
            return false
        }
        window.localStorage.setItem(
            "flowInfo",
            JSON.stringify({
                type:this.state.type,
                flowName:this.state.name,
                folderId:this.state.folderId
            })
        );
        this.props.hideModal();
        this.props.history.push(`/workFlowDesign?id=helloworld`)
    }
    _hideBuildFlow = () => {
        this.props.hideModal();
    }
    render() {
        return (
            <Modal
                    title='新建流程'
                    visible={true}
                    onOk={this._buildFlowOk}
                    onCancel={this._hideBuildFlow}
                    // okText = '确定'
                    // cancelText = '取消'
                >
                    <div style={{marginBottom: '10px'}}>
                        <span style={{width: '100px', float:'left', lineHeight: '32px'}}>流程名称：</span>
                        <Input style={{width: '300px'}} onChange={this._changeName} placeholder='请输入新建流程名称...' />
                    </div>
                    <div style={{marginBottom: '10px'}}>
                        <span style={{width: '100px', float:'left', lineHeight: '32px'}}>流程类型：</span>
                        <Select value={"1"} style={{ width: 300 }} disabled>
                            <Option value="1">创建计划</Option>
                            <Option value="2">新增表单</Option>
                            <Option value="3">填报内容</Option>
                        </Select>
                    </div>
                    <div style={{marginBottom: '10px'}}>
                        <span style={{width: '100px', float:'left', lineHeight: '32px'}}>所属文件夹：</span>
                        <TreeSelect
                            style={{ width: '300px' }}
                            dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                            placeholder="请选择所属文件夹"
                            treeDefaultExpandAll
                            onChange={this._changeFolder}
                        >
                            {this.renderTreeNodes(this.state.treeData)}
                        </TreeSelect>
                    </div>
                </Modal>
        )
    }
}


export default withRouter(CreateModal)