import React, { Component } from 'react';
import { Flow, withPropsAPI } from 'gg-editor';

class FlowWrapper extends Component {
    _onAfterChange = (e) => {
        if(e.item && e.item.type==="edge" && e.action === "add"){//新建连线
            // return false
            const { propsAPI } = this.props;
            setTimeout(()=>{
                propsAPI.executeCommand("undo");
            })
        }
    }
    render() {
        return (
            <Flow 
                data={this.props.flowData} 
                style={{height:584}} 
                onClick={this.props._onClick} 
                onAfterChange={this._onAfterChange}
                graph={{ edgeDefaultShape: 'flow-polyline'}} 
            />
        )
    }
}


export default withPropsAPI(FlowWrapper)