import React, { Component } from 'react'
import {Card, Button, Radio,message, Modal} from 'antd';
import PreviewWorkFlow from './previewWorkFlow';
import CreateModal from './createModal';
import styles from "./index.module.less";
import { postAction } from '@/axios'

export default class Step4 extends Component {
    constructor(props){
        super(props);
        this.state = {
            planTaskId:"",//任务id
            processName:"",//流程名称
            processId: null,//流程id
            list:[//流程列表
                // {processId:1,processName:"name"}
            ],
            showCreateModal:false,//新建工作流
        }
    }

    componentDidMount(){
        let fillPlanInfo = JSON.parse(window.localStorage.getItem("fillPlanInfo"));
        this.setState({
            planTaskId:fillPlanInfo.planTaskId,
        });
        if(fillPlanInfo.type==="edit"){
            this.getSelectedProgress(fillPlanInfo.planTaskId)
        }
        this.getWrokFlowListRequest();
    }
    //获取流程列表---请求
    getWrokFlowListRequest = () => {
        //创建表单类型，启用状态
        postAction("/ybt/wf/process/queryFolderProcessList?pageNum=1&pageSize=0",{processType:1,status:1}).then(res => {
            if(res.success){
                this.setState({
                    list:res.obj.rows.map(item => ({processId:item.processId,processName:item.processName})),
                    processId:res.obj.rows[0].processId
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //编辑时获取当前选择的流程---请求
    getSelectedProgress = (planTaskId) => {
        console.log("根据当前id获取当前选择的流程，未完成")
        this.setState({
            processId:''
        })
    }
    //切换选择流程
    onChange = e => {
        const process = this.state.list.filter(item => item.processId===e.target.value);
        this.setState({ 
            processId: process[0].processId,
            processName:process[0].processName
        });
    };
    //返回上一步
    toPre = () => {
        this.props.history.push('/fillPlan/step3')
    }
    //下一步
    toNext = () => {
        Modal.confirm({
            title:'提交确认',
            content:'请确认是否完成并提交？',
            okText:'确认',
            cancelText:'取消',
            onOk:()=>{
                const params = {
                    planTaskId:this.state.planTaskId,
                    processId:this.state.processId,
                    processName:this.state.processName
                }
                postAction("/ybt/planTask/startPlanTaskWorkFlow",params).then(res => {
                    if(res.success){
                        this.props.history.push('/fillPlan/step5');
                    }else{
                        message.error(res.obj)
                    }
                })
            }
        })
    }
    
    toCancle = () => {
        this.props.history.push('/fillPlan')
    }
    _addWorkFlow = () => {
        this.setState({showCreateModal:true})
    }

    render() {
        const radioStyle = {
            display: 'block',
            height: '30px',
            lineHeight: '30px',
        };
        return (
            <div style={{padding:"20px"}}>
                <Card  title="选择工作流程" style={{ width: "100%", boxShadow:"0px 0px 2px 2px #f1f1f1" }} bodyStyle={{padding:"0px"}}>
                    <div className={styles.workFlowContainer}>
                        <div className={styles.left}>
                            <p>请从以下工作流程中选择：</p>
                            {
                                this.state.list && this.state.list[0]?
                                <Radio.Group onChange={this.onChange} value={this.state.processId}>
                                    {
                                        this.state.list.map(item => <Radio key={item.processId} style={radioStyle} value={item.processId}>
                                                                        {item.processName}
                                                                    </Radio>
                                        )
                                    }
                                </Radio.Group>
                                :
                                null
                            }
                            <div style={{marginTop:20}}>
                                <Button type="primary" icon="plus" onClick={this._addWorkFlow}>新增工作流程</Button>
                            </div>
                        </div>
                        <div className={styles.right}>
                            <div className={styles.workPlace}>
                                <PreviewWorkFlow processId={this.state.processId}/>
                            </div>
                            <div className={styles.buttons}>
                                <Button  type="primary" ghost onClick={this.toPre}>上一步</Button>
                                <Button type="primary" style={{margin:"0px 10px"}} onClick={this.toNext}>完成</Button>
                                <Button onClick={this.toCancle}>取消</Button>
                            </div>
                        </div>
                    </div>
                </Card>
                {
                    this.state.showCreateModal?<CreateModal hideModal={()=>{this.setState({showCreateModal:false})}}/>:null
                }
            </div>
        )
    }
}
