import React, { Component } from 'react';
import GGEditor from 'gg-editor';
import { Drawer,message } from 'antd';
import { getAction } from '@/axios';
import FlowWrapper from'./flowWrapper';
import DetailModal from './detailModal';
import styles from './index.module.less';

export default class PreviewWorkFlow extends Component {
    constructor(props){
        super(props);
        this.state = {
            showDrawer:false,
            flowData:{
                nodes:[],
                edges:[],
            },
            nodeDetails:[],
            currentNode:{}
        }
    }
    componentDidUpdate(preProps){
        if(preProps.processId !== this.props.processId){
            this.getFlowDataById(this.props.processId)
        }
    }
    //通过流程id获取流程数据
    getFlowDataById = (id) => {
        if(id){
            getAction(`/ybt/wf/process/queryProcessById?processId=${id}`).then(res => {
                if(res.success){
                    let data = JSON.parse(res.obj)
                    this.setState({
                        flowData:{
                            nodes:data.nodeList,
                            edges:data.edgeList
                        },
                        nodeDetails:data.nodeDetails
                    })
                }else{
                    message.error("获取数据出错")
                }
            })
        }
    }
    //点击节点
    _onClick = (e) => {
        const judgeIds = ["n001","n002","n003","n004","e001","e002"];
        if(!e.item){
            return false
        }
        const model = e.item.getModel();
        if(judgeIds.indexOf(model.judgeId)!==-1){
            console.log("内置节点不可操作---点击");
        }else{
            if(e.item.type==="node"){//点击了节点 显示抽屉
                 this.setState({showDrawer:true,currentNode:e.item})
            }
        }
    }
     //关闭节点属性抽屉
    onCloseDrawer = () => {
        this.setState({ showDrawer: false});
    };
    render() {
        return (
            <GGEditor style={{padding:"20px"}} >
                {/* <Flow 
                    data={this.state.flowData} 
                    style={{height:584}} 
                    onClick={this._onClick} 
                    onAfterChange={this._onAfterChange}
                    graph={{ edgeDefaultShape: 'flow-polyline'}} 
                /> */}
                <FlowWrapper flowData={this.state.flowData} _onClick={this._onClick}/>
                <Drawer
                    className={styles.drawerModal}
                    title="节点属性"
                    width={380}
                    placement="right"
                    closable={false}
                    onClose={this.onCloseDrawer}
                    visible={this.state.showDrawer}
                    destroyOnClose={true}
                    // maskClosable={false}
                >
                    <DetailModal nodeDetails={this.state.nodeDetails}  onCloseDrawer={this.onCloseDrawer} currentNode={this.state.currentNode}/>
                </Drawer>
            </GGEditor>
        )
    }
}
