import React, { Component } from 'react';
import { Button, Radio, Input, Tag  } from 'antd';
import styles from './index.module.less';
export default class DrawerModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            nodeName:"",//节点名称
            userType:"1",//审批人类型1：人员，2：角色
            selectItems:[
                // {"participantId":"1002010334","participantName":"张三"}
            ], //选择的人员或者角色列表
            selectKeys:[], //选择的key
            auditType:"1",//审批类型1：会签，2：或签
            nodeDetails:[],//所有节点信息集合
        }
    }
    componentDidMount(){
        // console.log(this.props.currentNode.model);//当前点击的节点信息
        const currentNodeDetail = this.props.nodeDetails.find(item => item.id===this.props.currentNode.model.id) || {};
        console.log(currentNodeDetail);
        if(JSON.stringify(currentNodeDetail)==="{}"){
            this.setState({
                nodeDetails:this.props.nodeDetails,
                selectItems: [],
                selectKeys:[],
                nodeName:this.props.currentNode.model.label,
                userType:"1",
                auditType:"1",
                nodeId:this.props.currentNode.model.id
            })
        }else{
            this.setState({
                nodeDetails:this.props.nodeDetails,
                selectItems:currentNodeDetail.selectUsers || [],
                selectKeys:Array.isArray(currentNodeDetail.selectUsers)?currentNodeDetail.selectUsers.map(item => item.participantId):[],
                nodeName:currentNodeDetail.nodeName,
                userType:currentNodeDetail.userType,
                auditType:currentNodeDetail.auditType,
                nodeId:this.props.currentNode.model.id
            })
        }
    }
    render() {
        return (
            <React.Fragment>
                <div className={styles.content}>
                    <div className={styles.row}>
                        <div className={styles.label}><i>*</i>节点名称：</div>
                        <Input placeholder="请输入节点名称" style={{width:"240px"}} value={this.state.nodeName} disabled/>
                    </div>
                    <div className={styles.row}>
                        <div className={styles.label}>设置审批人：</div>
                        <Radio.Group value={this.state.userType} style={{lineHeight:"32px"}} disabled>
                            <Radio value={"1"}>指定人员</Radio>
                            <Radio value={"2"}>角色</Radio>
                        </Radio.Group>
                    </div>
                    <div className={styles.row}>
                        {
                           this.state.selectItems.map(item => (<Tag key={item.participantId} color="blue">{item.participantName}</Tag>))
                        }
                    </div>
                    <div style={{background:"#E8EBEE",height:1,margin:"16px 0"}}></div>
                    <div className={styles.row}>
                        <p>多人审核时采用的审核方式</p>
                        <Radio.Group value={this.state.auditType} disabled>
                            <Radio value="2">会签（须所有审核人同意）</Radio>
                            <br/><br/>
                            <Radio value="1">或签（一名审核人同意或拒绝即可）</Radio>
                        </Radio.Group>
                    </div>
                </div>
                <div className={styles.footer}>
                    <Button onClick={()=>{this.props.onCloseDrawer()}}>关 闭</Button>
                </div>
            </React.Fragment>
        )
    }
}
